import React, { useState } from "react";
import MonacoEditor from "@monaco-editor/react";
import axios from "axios";
import AppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import { Box } from "@mui/material";
import FileTabsComponent from "./FileTabsComponent";
import CodeEditorComponent from "./CodeEditorComponent";
import ProgramOutputComponent from "./ProgramOutputComponent";
import NetworkErrorModal from "./NetworkErrorModal";


const default_program =
  `class Test {
  public static void main() {

    int a;
    int b;

    a = 1;
    b = 2;

    Out.print(a+b);

  }
}`;

const CompilerFrontend = () => {
  const [sourceCode, setSourceCode] = useState(default_program); // State for source code
  //const [output, setOutput] = useState(""); // State for output from the backend

  const [compilerOutput, setCompilerOutput] = useState("");
  const [assemblyOutput, setAssemblyOutput] = useState("");
  const [displayErrorModal, setDisplayErrorModal] = useState(false);


  const [files, setFiles] = useState([
    {
      name: "file1.decaf",
      sourceCode: default_program,
    }
  ]
  );

  const [selectedFile, setSelectedFile] = useState('file1.decaf');


  // Function to handle compile button click
  const handleCompile = async () => {
    const endpoint = "https://flask-fire-637551824687.us-central1.run.app/compile"; // Replace with your backend endpoint
    const payload = { sourceCode };

    try {
      const response = await axios.post(endpoint, payload);
      //setOutput(response.data.output); // Set the output from backend

      setAssemblyOutput(response.data.output);
      setCompilerOutput(response.data.res);

    } catch (error) {
      if (error.response) {
        // The request was made and the server responded with a status code
        // that falls out of the range of 2xx
        console.error("Error code:", error.response.status); // The HTTP status code
        console.error("Error message:", error.response.data); // The response data, which might include a more detailed error message
      } else if (error.request) {
        // The request was made but no response was received
        setDisplayErrorModal(true);
        console.error("No response received:", error.request);
      } else {
        // Something happened in setting up the request that triggered an Error
        console.error("Error setting up the request:", error.message);
        setDisplayErrorModal(true);
      }
      //setOutput(
      //  `Error: ${error.response ? error.response.data.message : error.message} ${error}`
      //);
    }
  };

  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>

      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" component="div" sx={{ flexGrow: 1 }}>
            Decaf Compiler
          </Typography>
          <Button color="inherit">Home</Button>
          <Button color="inherit" onClick={handleClickOpen}>About</Button>
          <Button color="inherit">Contact</Button>
        </Toolbar>
      </AppBar>

      <Dialog open={open} onClose={handleClose}>
        <DialogTitle>Decaf Compiler</DialogTitle>
        <DialogContent>

          This is the modal content.
        </DialogContent>
        <DialogActions>
          <Button onClick={handleClose}>Close</Button>
        </DialogActions>
      </Dialog>

      <NetworkErrorModal open={displayErrorModal} onClose={() => {setDisplayErrorModal(false)}}/>

      <div style={{ padding: "20px", display: 'flex', justifyContent: 'space-between' }}>


        <Box sx={{ display: "flex", alignItems: "center", flexDirection: "column", width: "50%" }}>
          <FileTabsComponent fileNames={files.map(file => file.name)} 
          selectedFile={selectedFile}
           setSelectedFile={setSelectedFile}
           onCompile={handleCompile} />

          <CodeEditorComponent sourceCode={sourceCode} setSourceCode={setSourceCode}></CodeEditorComponent>

        </Box>



        

        {/* Output Display */}
        <Box sx={{ width: '50%' }}
          style={{
            //marginTop: "20px",
            height: "500px",
            //padding: "10px",
            backgroundColor: "#f4f4f4",
            //border: "1px solid #ddd",
          }}>
          <ProgramOutputComponent  assemblyOutput={assemblyOutput} commandLineOutput={compilerOutput} />
        </Box>
      </div>
      
      
    </div>
  );
};

export default CompilerFrontend;
