import React, { useState } from 'react';
import { Box, Tabs, Tab, Typography } from '@mui/material';
import MonacoEditor from "@monaco-editor/react";
import Terminal from 'react-console-emulator'

const keywords = ['move']

const ProgramOutputComponent = ({ assemblyOutput, commandLineOutput, otherContent }) => {
  const [selectedTab, setSelectedTab] = useState(0); // State for selected tab

  // Handle tab change
  const handleTabChange = (event, newValue) => {
    setSelectedTab(newValue);
  };

  const handleEditorWillMount = (monaco) => {
    // Register the custom language (e.g., Decaf)
    monaco.languages.register({ id: "decaf" });

    // Define the tokens and syntax highlighting
    monaco.languages.setMonarchTokensProvider("decaf", {
        keywords: keywords,
        operators: ["+", "-", "*", "/", "%", "&&", "||", "!", "=", "==", "!="],

        tokenizer: {
            root: [
                // Keywords
                [new RegExp(`\\b(${keywords.join("|")})\\b`), "keyword"],
                // Operators
                [/[\+\-\*\/%&|=!<>]+/, "operator"],
                // Numbers
                [/\b\d+\b/, "number"],
                // Strings
                [/".*?"/, "string"],
                // Identifiers
                [/\b[a-zA-Z_][a-zA-Z0-9_]*\b/, "identifier"],
            ],
        },
    });

    // Configure brackets, comments, and other language settings
    monaco.languages.setLanguageConfiguration("decaf", {
        comments: {
            lineComment: "//",
            blockComment: ["/*", "*/"],
        },
        brackets: [
            ["{", "}"],
            ["[", "]"],
            ["(", ")"],
        ],
        autoClosingPairs: [
            { open: "{", close: "}" },
            { open: "[", close: "]" },
            { open: "(", close: ")" },
            { open: '"', close: '"' },
        ],
    });
};

  return (
    <Box sx={{ width: '100%' }}>
      <Tabs
        value={selectedTab} // Track selected tab
        onChange={handleTabChange} // Handle tab change
        aria-label="compiler output tabs"
        variant="fullWidth"
      >
        <Tab label="Assembly Output" />
        <Tab label="Command Line Output" />
        <Tab label="Basic Blocks" />
      </Tabs>

      {/* Tab content based on selected tab */}
      <Box /*sx={{ padding: 2 }}*/>
        {selectedTab === 0 && (
            

            <MonacoEditor
                height="500px"
                width="100%"
                readOnly={true}
                //language="decaf" // Use the custom language ID
                //defaultValue={default_program}
                value={assemblyOutput}
                beforeMount={handleEditorWillMount} // Custom language setup
                //onChange={(value) => props.setSourceCode(value || "")}
                theme="vs-dark" // Optional: Monaco theme
                options={{domReadOnly: true}}
            />

          
        )}
        {selectedTab === 1 && (
            <Typography variant="body1" sx={{whiteSpace: 'pre-line'}}>
            {commandLineOutput}
          </Typography>
            
          
        )}
        {selectedTab === 2 && (
            <Typography variant="body1" sx={{whiteSpace: 'pre-line'}}>
            {"not implemented yet"}
          </Typography>
          
        )}
      </Box>
    </Box>
  );
};

export default ProgramOutputComponent;
